import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import {Badge, Card, Table} from 'antd';
import ApiService from "../../../../../../services/api.service";
import {COLOR_DEFAULT_SELECTION} from "../../../../../../configs/AppConfig";
function mapStateToProps(state) {
 return {

 };
}

function CategoryOverview(props)  {
    const [ data, setData ] = useState([])
    const [ loading, setLoading ] = useState(false)
    const [ pagination, setPagination ] = useState({
        page:1,
        limit:10,
        total_items:0
    })
    const [ params, setParams ] = useState({
        page:1,
        limit:10
    })

    useEffect(()=> {
        setLoading(true)
        new ApiService({
            url:`/api/v1/audio/category`,
            config : {
                params: {
                    ...params
                }
            }
        }).get()
            .then((response)=> {
                setData([...response?.data])
                setPagination({
                    ...pagination,
                    ...response?.pagination,
                    ...response?.paginate,
                })
                setLoading(false)
            })
            .catch((err)=> {
                setLoading(false)
                setData([])
            })
    },[params])

    const columns = [
        {
            dataIndex:'id',
            title:"No",
            width:70,
            render: function (_,val,index){
                return index + 1
            }
        },
        {
            dataIndex:"identifier",
            title:"Slug"
        },
        {
            dataIndex:['data','name'],
            title:"Display Name",
            render: (_,val)=> {
                return <Badge color={val?.data?.color ?? COLOR_DEFAULT_SELECTION} text={_}/>
            }
        },
        {
            dataIndex:"type",
            title:"Type"
        },
    ]
  return (
   <Card bordered={false}>
       <Table
           columns={columns ?? []}
           loading={loading ?? false}
           dataSource={data ?? []}
       />
   </Card>
  );
}

export default connect(
 mapStateToProps,
)(CategoryOverview);